module.exports = "import { Component } from '@angular/core';\n\n@Component({\n  selector: 'nz-demo-dropdown-dropdown-button',\n  template: `\n    <div style=\"height: 28px;\">\n      <nz-dropdown-button (nzClick)=\"log()\">\n        DropDown\n        <ul nz-menu>\n          <li nz-menu-item>1st menu item</li>\n          <li nz-menu-item>2nd menu item</li>\n          <li nz-submenu>\n            <span title>sub menu</span>\n            <ul>\n              <li nz-menu-item>3rd menu item</li>\n              <li nz-menu-item>4th menu item</li>\n            </ul>\n          </li>\n        </ul>\n      </nz-dropdown-button>\n      <nz-dropdown-button nzDisabled>\n        DropDown\n        <ul nz-menu>\n          <li nz-menu-item>\n            <a>1st menu item</a>\n          </li>\n          <li nz-menu-item>\n            <a>2nd menu item</a>\n          </li>\n          <li nz-menu-item>\n            <a>3rd menu item</a>\n          </li>\n        </ul>\n      </nz-dropdown-button>\n      <nz-dropdown>\n        <button nz-button nz-dropdown><span>Button</span> <i nz-icon type=\"down\"></i></button>\n        <ul nz-menu>\n          <li nz-menu-item>\n            <a>1st menu item</a>\n          </li>\n          <li nz-menu-item>\n            <a>2nd menu item</a>\n          </li>\n          <li nz-menu-item>\n            <a>3rd menu item</a>\n          </li>\n        </ul>\n      </nz-dropdown>\n    </div>\n  `,\n  styles  : [\n      `\n      nz-dropdown-button {\n        margin-right: 8px;\n      }\n    `\n  ]\n})\nexport class NzDemoDropdownDropdownButtonComponent {\n  log(): void {\n    console.log('click dropdown button');\n  }\n}\n"